import React, { Component } from "react";
import P5Wrapper from "react-p5-wrapper";
import sketch from "./sketch";
import "../physics-sim.css";

// Since we can not use the p5 gui, we need to recreate the gui with react in this simulator
// we will pass the props over to the the p5wrapper
class Simulator extends Component {
  constructor(props) {
    super(props);
    this.state = {
      leftCharge: 0,
      rightCharge: 0,
      particleSpeed: 250,
      particleCharge: 0,
      particleAngle: 90,
      canvasWidth: 0,
      canvasHeight: 0
    };

    this.setValue = this.setValue.bind(this);
    this.getCanvasDimensions = this.getCanvasDimensions.bind(this);
  }

  saveRef = ref => (this.containerNode = ref);

  getCanvasDimensions() {
    if (this.state.canvasHeight !== window.innerHeight / 1.7) {
      this.setState({ canvasHeight: window.innerHeight / 1.7 });
    }
    if (this.state.canvasWidth !== this.div.clientWidth) {
      this.setState({ canvasWidth: this.div.clientWidth });
    }
  }

  componentDidMount() {
    this.getCanvasDimensions();
    window.addEventListener("resize", this.getCanvasDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.getCanvasDimensions);
  }

  shouldComponentUpdate(nextProps, nextState) {
    return (
      this.state.leftCharge !== nextState.leftCharge ||
      this.state.rightCharge !== nextState.rightCharge ||
      this.state.particleSpeed !== nextState.particleSpeed ||
      this.state.particleCharge !== nextState.particleCharge ||
      this.state.particleAngle !== nextState.particleAngle ||
      this.state.canvasHeight !== nextState.canvasHeight ||
      this.state.canvasWidth !== nextState.canvasWidth
    );
  }

  setValue(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  render() {
    return (
      <div className="container" style={{ height: "75%", width: "100%" }}>
        <div className="row">
          <div
            className="col-lg-12"
            ref={div => {
              this.div = div;
            }}
          >
            <P5Wrapper
              sketch={sketch}
              leftCharge={this.state.leftCharge}
              rightCharge={this.state.rightCharge}
              particleSpeed={this.state.particleSpeed}
              particleCharge={this.state.particleCharge}
              particleAngle={this.state.particleAngle}
              canvasWidth={this.state.canvasWidth}
              canvasHeight={this.state.canvasHeight}
            />
          </div>
        </div>
        <div className="row">
          <div className="col-lg-3" />
          <div className="col-lg-6">
            <table style={{ width: "100%" }}>
              <tr>
                <td>
                  <h6 className="d-inline pr-3">
                    {this.state.canvasWidth > 589
                      ? "LEFT CHARGE"
                      : "MAIN CHARGE"}
                  </h6>
                </td>
                <td>
                  <input
                    type="range"
                    min={-1}
                    max={1}
                    step={0.1}
                    value={this.state.leftCharge}
                    className="simulator-slider d-inline"
                    name="leftCharge"
                    onChange={this.setValue}
                  />
                </td>
              </tr>
              {this.state.canvasWidth > 589 && <br />}
              {this.state.canvasWidth > 589 && (
                <tr>
                  <td>
                    <h6 className="d-inline pr-3 text-left">RIGHT CHARGE</h6>
                  </td>
                  <td>
                    <input
                      type="range"
                      min={-1}
                      max={1}
                      step={0.1}
                      value={this.state.rightCharge}
                      className="simulator-slider d-inline"
                      name="rightCharge"
                      onChange={this.setValue}
                    />
                  </td>
                </tr>
              )}
              <br />
              <tr>
                <td>
                  <h6 className="d-inline pr-3">PARTICLE SPEED</h6>
                </td>
                <td>
                  <input
                    type="range"
                    min={0}
                    max={1000}
                    step={5}
                    value={this.state.particleSpeed}
                    className="simulator-slider d-inline"
                    name="particleSpeed"
                    onChange={this.setValue}
                  />
                </td>
              </tr>
              <br />
              <tr>
                <td>
                  <h6 className="d-inline pr-3">PARTICLE CHARGE</h6>
                </td>
                <td>
                  <input
                    type="range"
                    min={-1}
                    max={1}
                    step={0.1}
                    value={this.state.particleCharge}
                    className="simulator-slider d-inline"
                    name="particleCharge"
                    onChange={this.setValue}
                  />
                </td>
              </tr>
              <br />
              <tr>
                <td>
                  <h6 className="d-inline pr-3">PARTICLE ANGLE</h6>
                </td>
                <td>
                  <input
                    type="range"
                    min={-180}
                    max={180}
                    step={5}
                    value={this.state.particleAngle}
                    className="simulator-slider d-inline"
                    name="particleAngle"
                    onChange={this.setValue}
                  />
                </td>
              </tr>
            </table>
          </div>
          <div className="col-lg-3" />
        </div>
      </div>
    );
  }
}

export default Simulator;
