import React, { Component } from "react";
import { Link } from "react-router-dom";
import $ from "jquery";
import { isMobile } from "react-device-detect";
import "./layout.css";

export default class Navbar extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    window.addEventListener("resize", () => {
      this.forceUpdate();
    });
  }

  dropdownNavbar(button) {
    if (button === "home-button") {
      if ($("#nav-dropdown").hasClass("show")) {
        $("#nav-dropdown").toggleClass("show");
      }
    } else {
      $("#nav-dropdown").toggleClass("show");
    }
  }

  /**
   * Renders the navbar component.
   */
  render() {
    return (
      <nav
        id="main-navbar"
        className="navbar space-between navbar-expand-sm fixed-top bg-navbar navbar-dark"
      >
        <div
          className="navbar-left"
          onClick={() => this.dropdownNavbar("home-button")}
        >
          <Link className="navbar-brand pl-1 ml-1 welcome-header-text" to="">
            NDOHERTY.DESIGN
          </Link>
        </div>
        <button
          type="button"
          className="navbar-toggler btn-white rounded"
          data-toggle="collapse"
          data-target="#nav-dropdown"
        >
          <i className="fas fa-bars p-0 m-0 text-center" />
        </button>

        <div
          className="justify-flex-end collapse navbar-collapse "
          id="nav-dropdown"
        >
          <div className="navbar-right">
            <ul className="navbar-nav mt-lg-0 ">
              <li
                className="nav-item active"
                onClick={() => this.dropdownNavbar("contact-button")}
              >
                <Link className="nav-link pl-0 pr-2 mr-1" to="/portfolio">
                  PORTFOLIO
                </Link>
              </li>
              <li
                className="nav-item active"
                onClick={() => this.dropdownNavbar("contact-button")}
              >
                <Link className="nav-link pl-0 pr-2 mr-1" to="/contact">
                  CONTACT
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    );
  }
}
