import p5 from "p5";

export default function sketch(p) {
  //CONSTANT PARAMETERS
  var fcX;
  var fcY;
  var particles = [];
  var fixedCharge1, fixedCharge2;
  var title = "Electrostatic Force Simulation";

  //CONVERSIONS
  var pixToM = 1 / 100;

  //VARIABLE PARAMETERS
  var FixedChargeLeft = 0;

  var FixedChargeRight = 0;

  //MULTIPLY BY 1 * pow(10, 5) BEFORE USING IN CALCULATIONS
  var InitialParticleSpeed = 250;

  var ParticleCharge = 0;

  var InitialParticleAngle = 0;

  //MULTIPLY BY 9.11 * pow(10, -31) BEFORE USING IN CALCULATIONS
  var ParticleMass = 1;

  var widthOfSim = 0;
  var heightOfSim = 0;
  var xOffset = 9999;
  var yOffset = 9999;

  p.myCustomRedrawAccordingToNewPropsHandler = function(props) {
    if (props.canvasHeight) {
      heightOfSim = props.canvasHeight;
      p.resizeCanvas(widthOfSim, heightOfSim);
      p.setupCharges();
    }
    if (props.canvasWidth) {
      widthOfSim = props.canvasWidth;
      p.resizeCanvas(widthOfSim, heightOfSim);
      p.setupCharges();
    }
    if (props.leftCharge) {
      FixedChargeLeft = props.leftCharge;
    }
    if (props.rightCharge) {
      FixedChargeRight = props.rightCharge;
    }
    if (props.particleSpeed) {
      InitialParticleSpeed = props.particleSpeed;
    }
    if (props.particleCharge) {
      ParticleCharge = props.particleCharge;
    }
    if (props.particleAngle) {
      InitialParticleAngle = props.particleAngle;
    }
    if (props.xOffset) {
      xOffset = props.xOffset;
    }
    if (props.yOffset) {
      yOffset = props.yOffset;
    }
  };

  p.setup = function() {
    p.createCanvas(widthOfSim, heightOfSim);
    p.setupCharges();
  };

  p.setupCharges = function() {
    if (widthOfSim > 689) {
      fixedCharge1 = new Fixed(
        widthOfSim / 3,
        heightOfSim / 2,
        FixedChargeLeft
      );
      fixedCharge2 = new Fixed(
        (2 * widthOfSim) / 3,
        heightOfSim / 2,
        FixedChargeRight
      );
    } else {
      fixedCharge1 = new Fixed(
        widthOfSim / 2,
        heightOfSim / 2,
        FixedChargeLeft
      );
      fixedCharge2 = null;
    }
  };

  p.draw = function() {
    p.background(235, 245, 255);

    p.push();
    p.textSize(14);
    p.fill(0);
    p.textFont("Helvetica");
    p.textStyle(p.BOLD);
    p.text(title, 20, 3, 440, 50);
    p.pop();
    fixedCharge1.display();

    if (fixedCharge2) {
      fixedCharge2.display();
    }

    //DRAW DIRECTION ARROW

    p.push();
    p.noStroke();
    p.translate(p.mouseX, p.mouseY);
    p.rotate(p.radians(InitialParticleAngle));
    p.fill(0);
    p.rect(0, -2, 24, 4);
    p.triangle(24, -4, 34, 0, 24, 4);
    p.pop();

    fixedCharge1.charge = FixedChargeLeft;
    if (fixedCharge2) {
      fixedCharge2.charge = FixedChargeRight;
    }

    //UPDATE PARTICLES
    for (var i = 0; i < particles.length; i++) {
      //CALCULATE FORCE
      var eForce1 = fixedCharge1.calculateForce(particles[i]);
      particles[i].applyForce(eForce1);

      if (fixedCharge2) {
        var eForce2 = fixedCharge2.calculateForce(particles[i]);
        particles[i].applyForce(eForce2);
      }

      particles[i].update();
      particles[i].display();

      if (particles[i].checkOutOfBounds()) {
        particles.splice(i, 1);
      }
    }
  };

  p.mousePressed = function() {
    if (
      !(
        p.mouseX > widthOfSim ||
        p.mouseX < 0 ||
        p.mouseY > heightOfSim ||
        p.mouseY < 0
      )
    ) {
      particles.push(
        new Particle(ParticleMass, p.mouseX, p.mouseY, ParticleCharge)
      );
    }
  };

  var Fixed = function(x, y, q) {
    this.x = x;
    this.y = y;
    this.charge = q;
  };

  Fixed.prototype.calculateForce = function(particle) {
    var r =
      p5.Vector.dist(p.createVector(this.x, this.y), particle.position) *
      pixToM;

    //Calculate force
    var f = (particle.charge * this.charge) / p.sq(r);

    //Calculate direction
    var eForce = p.createVector(
      particle.position.x - this.x,
      particle.position.y - this.y
    );

    eForce.normalize();
    eForce.mult(f);
    return eForce;
  };

  Fixed.prototype.display = function() {
    p.fill("rgba(65, 105, 255, .85)");
    p.noStroke();
    p.ellipseMode(p.CENTER);
    p.ellipse(this.x, this.y, 50, 50);

    if (this.charge < 0) {
      p.push();
      p.fill(0);
      p.rectMode(p.CENTER);
      p.translate(this.x, this.y);
      p.rect(0, 0, 30, 4);
      p.pop();
    } else if (this.charge > 0) {
      p.push();
      p.fill(0);
      p.rectMode(p.CENTER);
      p.translate(this.x, this.y);
      p.rect(0, 0, 30, 4);
      p.rect(0, 0, 4, 30);
      p.pop();
    }
  };

  function Particle(m, x, y, c) {
    this.mass = m;
    this.charge = c;
    this.position = p.createVector(x, y);
    this.velocity = p.createVector(
      InitialParticleSpeed * pixToM * p.cos(p.radians(InitialParticleAngle)),
      InitialParticleSpeed * pixToM * p.sin(p.radians(InitialParticleAngle))
    );
    this.acceleration = p.createVector(0, 0);
  }

  Particle.prototype.applyForce = function(force) {
    var f = p5.Vector.div(force, this.mass);
    this.acceleration.add(f);
  };

  Particle.prototype.update = function() {
    this.velocity.add(this.acceleration);
    this.position.add(this.velocity);
    this.acceleration.mult(0);
  };

  Particle.prototype.display = function() {
    p.fill("rgba(255, 0, 0, .85)");
    p.ellipse(this.position.x, this.position.y, 14, 14);

    if (this.charge > 0) {
      p.push();
      p.fill(0);
      p.rectMode(p.CENTER);
      p.translate(this.position.x, this.position.y);
      p.rect(0, 0, 8, 2);
      p.rect(0, 0, 2, 8);
      p.pop();
    } else if (this.charge < 0) {
      p.push();
      p.fill(0);
      p.rectMode(p.CENTER);
      p.translate(this.position.x, this.position.y);
      p.rect(0, 0, 8, 2);
      p.pop();
    } else {
    }
  };

  Particle.prototype.checkOutOfBounds = function() {
    if (
      this.position.x > widthOfSim ||
      this.position.x < 0 ||
      this.position.y > heightOfSim ||
      this.position.y < 0
    ) {
      return true;
    }
  };
}
